body {
  background: #002833;
  color: #f9f9f9;
  padding: 8px 10px;
}
.cli {
  width: 100%;
  font-family: 'Inconsolata', monospace;
  position: relative;
}
.cli .output-text a {
    text-decoration: underline;
}
.cli div {
    line-height: 1.3em;
}
.cli input {
    position: absolute;
    bottom: 0;
    color: transparent;
    background: transparent;
    border: none;
    outline: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    width: 1px;
}
.cli a {
    color: #f9f9f9;
    text-decoration: none;
}
.cli a:hover {
      text-decoration: underline;
}
.cursor {
  background: #f9f9f9;
  display: inline-block;
  position: relative;
}
.cursor.blink {
    -webkit-animation: 1s blink step-end infinite;
            animation: 1s blink step-end infinite;
}
.rainbow {
  -webkit-animation: 1s ease-in 1s infinite rainbow;
          animation: 1s ease-in 1s infinite rainbow;
}
.user-prompt {
  color: #93a1a1;
}
.add-buffer {
  display: inline-block;
  margin-right: 6px;
}
